import list from "@/components/goods-list.vue";
import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue";
import {
    mapState
} from 'vuex';

export default {
  data() {
    return {
      mescroll: null, //mescroll实例对象
      upOption:{
        page: {
        // 	num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
        	size: 6, // 每页数据的数量
        },
        noMoreSize: 4, //如果列表已无数据,可设置列表的总数量要大于半页才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看; 默认5
        empty:{
          tip: '~ 抱歉，这里暂时没有你想要的内容 ~', // 提示
          btnText: '返回首页'
        }
      },
      tabType: 0, // 菜单
      list: [],
      keyword: '',
      tipShow: false
    };
  },
  components: {
    list,
    MescrollUni
	},
	computed: {
			...mapState(['showModal','clipText'])
	},
	onShow(){
			this.$common.searchClip();
	},
	updated() {
			this.showM();
	},
  onLoad (options) {
    // console.log(options)
    this.keyword = options.key
  },
  methods: {
		 	showM(){
					if(this.showModal){
							this.$refs.modal.showModal();
					}
			},
    // mescroll组件初始化的回调,可获取到mescroll对象
			mescrollInit(mescroll) {
				this.mescroll = mescroll;
			},
			/*下拉刷新的回调 */
			downCallback(mescroll) {
				// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
				// loadSwiper();
				// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
				mescroll.resetUpScroll()
			},
			/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
			upCallback(mescroll) {
				//联网加载数据
				this.getListDataFromNet((curPageData)=>{
					//联网成功的回调,隐藏下拉刷新和上拉加载的状态;
					mescroll.endSuccess(curPageData.length);
					//设置列表数据
					if(mescroll.num == 1) this.list = []; //如果是第一页需手动制空列表
					this.list= curPageData ; //追加新数据
				}, () => {
					//联网失败的回调,隐藏下拉刷新的状态
					mescroll.endErr();
				})
			},
			//点击空布局按钮的回调
			emptyClick(){
				uni.switchTab({
						url: '/pages/index/index'
				});
			},

			// 切换菜单
			changeTab (type) {
				if (this.tabType !== type) {
					this.tabType = type
					this.pdList = []// 在这里手动置空列表,可显示加载中的请求进度
					this.mescroll.resetUpScroll()// 刷新列表数据
				}
			},

			/*联网加载列表数据
			在您的实际项目中,请参考官方写法: http://www.mescroll.com/uni.html#tagUpCallback
			请忽略getListDataFromNet的逻辑,这里仅仅是在本地模拟分页数据,本地演示用
			实际项目以您服务器接口返回的数据为准,无需本地处理分页.
			* */
			async getListDataFromNet(successCallback,errorCallback) {
        try {
					const list = await this.$request.post("/search/index", {
						data: {
							keywords: this.keyword,
						}
					});
					let listArr = []
					for (const i in list.data.data) {
							let afterPrice = (list.data.data[i].discountPrice - list.data.data[i].commissionAmount).toFixed(1);
							list.data.data[i].afterPrice = afterPrice;
							listArr.push(list.data.data[i])
					}
          // for (const item of list.data.data) {
          //   let afterPrice = (item.origin_price - item.coupon_money).toFixed(1);
          //   item.afterPrice = afterPrice;
          // }

					successCallback && successCallback(listArr);
				} catch (error) {
					console.error("error:", error);
				}
		}
  }
}
